


// DataIngestion is the ingestion pipeline configuration
export class DataIngestion {
 
    // Input files locations 
    public inputFilesURIs: string[];
 
    // Input files extensions (filter by extensions) - ignore if empty 
    public inputFilesExt: string[];
 
    // Usage time window for aggregation (in seconds) 
    public usageTimeWindowSec: number;
 
    // Session time window for aggregation (in seconds) 
    public sessionTimeWindowSec: number;
 
    // Usage data output locations 
    public usageOutputURIs: string[];
 
    // Usage data output locations 
    public sessionOutputURIs: string[];
 
    // Usage data output locations 
    public dNSOutputURIs: string[];
 
    // Where to back-up raw data files 
    public rawDataBackupURI: string;
 
    // Flag to indicate overwrite existing files 
    public overwriteFiles: boolean;
 
    // Post-processing file action: DELETE | RENAME | ... 
    public postAction: string;
 
     constructor(inputFilesURIs?: string[], inputFilesExt?: string[], usageTimeWindowSec?: number, sessionTimeWindowSec?: number, usageOutputURIs?: string[], sessionOutputURIs?: string[], dNSOutputURIs?: string[], rawDataBackupURI?: string, overwriteFiles?: boolean, postAction?: string) { 
        if (inputFilesURIs !== undefined) { this.inputFilesURIs = inputFilesURIs; }
        if (inputFilesExt !== undefined) { this.inputFilesExt = inputFilesExt; }
        if (usageTimeWindowSec !== undefined) { this.usageTimeWindowSec = usageTimeWindowSec; }
        if (sessionTimeWindowSec !== undefined) { this.sessionTimeWindowSec = sessionTimeWindowSec; }
        if (usageOutputURIs !== undefined) { this.usageOutputURIs = usageOutputURIs; }
        if (sessionOutputURIs !== undefined) { this.sessionOutputURIs = sessionOutputURIs; }
        if (dNSOutputURIs !== undefined) { this.dNSOutputURIs = dNSOutputURIs; }
        if (rawDataBackupURI !== undefined) { this.rawDataBackupURI = rawDataBackupURI; }
        if (overwriteFiles !== undefined) { this.overwriteFiles = overwriteFiles; }
        if (postAction !== undefined) { this.postAction = postAction; }
    }

}


