import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpRequest, HttpResponse, HttpErrorResponse } from '@angular/common/http';  // replaces previous Http service
import { map, catchError } from 'rxjs/operators';
import { Observable, throwError } from 'rxjs';

const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type':  'application/json',
    'Cache-Control': 'no-cache, no-store',
    'Access-Control-Allow-Origin': '*',
    'Access-Control-Allow-Headers': 'Content-Type, Content-Length, Accept-Encoding, X-CSRF-Token, X-API-KEY, X-ACCESS-TOKEN, X-TIMEZONE, accept, origin, Cache-Control, X-Requested-With, Authorization, Content-Disposition, Content-Filename',
    'Access-Control-Exposed-Headers': 'X-API-KEY, X-ACCESS-TOKEN, X-TIMEZONE, Content-Disposition, Content-Filename',
  })
};


// Utility class for all REST services with common functions
@Injectable()
export class RestUtil {

  // Constructor with injected authentication service
  constructor(private http: HttpClient) { }

  // Upload is HTTP POST action but the body is File object
  upload(file: File, url: string, ...params: string[]) {

    const resourceUrl = this.buildUrl(url, ...params);

    const formData: FormData = new FormData();
    formData.append('fileKey', file, file.name);

    const req = new HttpRequest('POST', resourceUrl, formData, {
      reportProgress: false,
      responseType: 'json',
    });
    return this.http.request(req);
  }

  // Download is HTTP GET action but the content is blob
  download(fileName: string, url: string, ...params: string[]) {
    const resourceUrl = this.buildUrl(url, ...params);

    let downloadLink = fileName

    // extract file name
    params.forEach(p => {
      let arr = p.split('=');
      if (arr.length > 1) {
        if (arr[0].toLowerCase() === 'filename') {
          downloadLink = arr[1];
        }
      }
    });


    // Set content type for: json / csv / xml / pdf
    let contentType = 'application/json';
    if (downloadLink.toLowerCase().endsWith('csv')) {
      contentType = 'text/csv';
    } else if (downloadLink.toLowerCase().endsWith('xml')) {
      contentType = 'text/xml';
    } else if (downloadLink.toLowerCase().endsWith('pdf')) {
      contentType = 'application/pdf';
    }

    return this.http.get(resourceUrl, {
      responseType: 'blob',
      reportProgress: true,
      observe: 'events',
      headers: new HttpHeaders({ 'Content-Type': contentType })
    });
  }

  // HTTP GET action
  get<T>(url: string, ...params: string[]): Observable<T> {
    const resourceUrl = this.buildUrl(url, ...params);
    return this.http
        .get<T>(resourceUrl, httpOptions)
        .pipe(
            //map((res: any) => this.processResponse(res)),
            catchError(this.handleError),
        );
  }

  // HTTP POST action
  post<T>(url: string, body?: string, ...params: string[]): Observable<HttpResponse<T>> {
    const resourceUrl = this.buildUrl(url, ...params);
    return this.http
        .post<HttpResponse<T>>(resourceUrl, body, httpOptions)
        .pipe(
            //map((res: any) => this.processResponse(res)),
            catchError(this.handleError)
        );
  }

  // HTTP PUT action
  put<T>(url: string, body?: string, ...params: string[]): Observable<HttpResponse<T>> {
    const resourceUrl = this.buildUrl(url, ...params);
    return this.http
        .put<HttpResponse<T>>(resourceUrl, body, httpOptions)
        .pipe(
            //map((res: Observable<T>) => this.processResponse(res)),
            catchError(this.handleError)
        );
  }

  // HTTP DELETE action
  delete<T>(url: string, ...params: string[]): Observable<HttpResponse<T>> {
    const resourceUrl = this.buildUrl(url, ...params);
    return this.http
        .delete<HttpResponse<T>>(resourceUrl, httpOptions)
        .pipe(
            //map((res: any) => this.processResponse(res)),
            catchError(this.handleError)
        );
  }

  // Construct URL with parameters
  private buildUrl(url: string, ...params: string[]) {
    return (params === null) ? url : (params.length === 0) ? url : `${url}${params && params.length > 0 ? '?'+params.join('&') : ''}`;
  }

  // Process the response, if code is not 0, throw an error
  private processResponse(response: any) {
    // If response code is not 0, throw an error
    if (response.code !== 0) {
      throw { code: response.code, message: response.error };
    }
  }

  // Handle error
  private handleError(error: HttpErrorResponse) {
    if (error.status === 0) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong.
      console.error(
          `Backend returned code ${error.status}, body was: `, error.error);
    }
    // Return an observable with a user-facing error message.
    return throwError(() => new Error('Something bad happened; please try again later.'));
  }

}
